.clock {
  border: 6px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin-block: 10vmin;
  margin-inline: auto;
  display: flex;
  position: relative;
}

.clock__hand {
  transform-origin: bottom;
  display: flex;
  position: absolute;
}

.clock__hand--second {
  background-color: green;
  width: 10px;
  height: 50%;
  animation: 5s linear forwards move;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.clock__hand--minute {
  background-color: #00f;
  width: 20px;
  height: 25%;
  animation: 60s linear forwards move;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
}

.clock__center {
  z-index: 1;
  background-color: #ff0;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  margin: auto;
  display: flex;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

